.btn-wrapper {
    height: 3em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4em;
    background-color: #FFF;
    color: #293944;
	flex: 1; /* to all element have same length*/
    border: 1px solid #293944;
}

.btn-wrapper:hover {
    background-color: #EEE;
}

.operator {
    background-color: #EC9770;
    color: #293944;
}

.operator:hover {
    background-color: #D3815B;
}
